<template>
<figure class="banner">
  <a :href="Banners.href" >
  <img :src="Banners.Url" alt="" />
  </a>
</figure>
</template>
<script>
export default {
  name: 'Banner',
  data () {
    return {
    }
  },
  methods: {
  },
  props: ['Banners']
}
</script>
<style>
.banner a{
  display: block;
  width:100%;
}
.banner a img {
  width:100%;
}
</style>



