<template>
  <div class="hello">
    <router-view></router-view>
    <Bottom></Bottom>
  </div>
</template>

<script>
import Banner from '@/components/Banner.vue'
import Bottom from '@/components/Bottom.vue'
export default {
  name: 'HelloWorld',
  data () {
    return {
      bannerData: {
        des: '123',
        href: '/apprentice',
        Url: 'http://browser.umeweb.com/images/up/wealth-center0712.png'
      }
    }
  },
  components: {
    Banner,
    Bottom
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



